body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.pseudolink {
  text-decoration: underline;
  cursor: pointer;
  color: #222299;
}

.display-linebreak {
  white-space: pre-line;
}

.icon-link {
  color: #414549;
}

.icon-link:hover {
  color: #000000;
}


